<script lang="ts">
	import { COLS, ROWS } from "../../utils";
	import { username } from "../../stores";
	import { Tile } from "../board";
	export let visible: boolean;
    
    let rowWidth = 44*COLS-4;
    
    function isBlank(str) {
        return (!str || /^\s*$/.test(str));
    }
    
    function setUsername(e) {

        const form = document.querySelector('form');
        const submitResponse = document.querySelector('#response');

        // Capture the form data
        let data = { };
        Array.from(form).map(input => (data[input.id] = input.value));


        if(isBlank(data.username))
            submitResponse.innerHTML = 'Usernames cannot be blank!'
        else {
            $username = data.username;
            submitResponse.innerHTML = 'Welcome {$username}!';
        }
    };
</script>

<h3>Welcome
{#if ($username)}
    {$username}!
{:else}
    to N-erdle!
{/if}
</h3> 
<div>On each day of ALS Building Infrastructure Week, there will be a power & utilities-themed word to guess – the person who can get all three in the fewest guesses will win a special prize…</div>

{#if (!($username) || ($username === ""))}
<h3>Sign up</h3>
<p>To get started, choose a username (this will be used to identify the winner). If you have played before, use the same username here!</p>
<div>
    <form on:submit|preventDefault={setUsername}>
    <div class="form-group">
        <label for="name">Username</label>
        <input type="text" id="username"  placeholder="Username">
    </div>
    <button type="submit" class="btn btn-primary">Submit</button>
    </form>
    <h4 id="response">&nbsp;</h4>
</div>

{:else}
<h3>how to play</h3>

<div>Guess the <strong>N-ERDLE</strong> in {ROWS} tries.</div>
<div>Each guess must be a valid {COLS}-letter word. Press enter to submit a guess. The colour of the tiles will then change as follows.
</div>
<div class:complete={visible} class="examples" style="--tutorial-row-width: {rowWidth}px; --cols: {COLS}">
	<div><strong>Examples</strong></div>
	<div class="row">
		<Tile value="a" state="correct" />
		<Tile value="l" state="nil" />
		<Tile value="l" state="nil" />
		<Tile value="0" state="nil" />
		<Tile value="y" state="nil" />
	</div>
	<div>The letter <strong>A</strong> is in the word and in the correct spot.</div>
	<div class="row">
		<Tile value="p" state="nil" />
		<Tile value="i" state="present" />
		<Tile value="p" state="nil" />
		<Tile value="e" state="nil" />
		<Tile value="s" state="nil" />
	</div>
	<div>The letter <strong>I</strong> is in the word but in the wrong spot.</div>
	<div class="row">
		<Tile value="w" state="nil" />
		<Tile value="a" state="nil" />
		<Tile value="t" state="nil" />
		<Tile value="e" state="absent" />
		<Tile value="r" state="nil" />
	</div>
	<div>The letter <strong>E</strong> is not in the word in any spot.</div>
</div>
<div>A new N-ERDLE will be available at midnight each day!</div>
{/if}

<style lang="scss">
	div {
		margin: 10px 0;
        font-stretch: 100%;
        font-weight: 400;
	}
	.examples {
		border-top: 1px solid var(--border-primary);
		border-bottom: 1px solid var(--border-primary);
		:global(.row > *) {
			height: 100%;
		}
		&:not(.complete) :global(.row .back) {
			transition-delay: 0.3s;
		}
	}
	.row {
		display: grid;
		grid-template-columns: repeat(var(--cols), 1fr);
		height: 40px;
        width: var(--tutorial-row-width);
		grid-gap: 5px;
        font-size: 1.8em;
        line-height: 1.4em;
        vertical-align: middle;
	}
</style>
