<script lang="ts">
	import Header from "./Header.svelte";
	import { Board } from "./board";
	import Keyboard from "./keyboard";
	import Modal from "./Modal.svelte";
	import { onMount, setContext } from "svelte";
	import Settings from "./settings";
	import {
		Share,
		Separator,
//		Definition,
		Tutorial,
		Statistics,
		Distribution,
		Timer,
		Toaster,
        Form,
	} from "./widgets";
	import {
		contractNum,
		DELAY_INCREMENT,
		PRAISE,
		getState,
		modeData,
		checkHardMode,
		ROWS,
		COLS,
		newSeed,
		createNewGame,
        getWordNumber,
		createLetterStates,
		words,
	} from "../utils";
	import { letterStates, hardMode, mode, username } from "../stores";

	export let word: string;
	export let stats: Stats;
	export let game: GameState;
	export let toaster: Toaster;

	setContext("toaster", toaster);

	// implement transition delay on keys
	const delay = DELAY_INCREMENT * ROWS + 800;

	let showTutorial = false; 
	let showSettings = false;
	let showStats = false;
	let showRefresh = false;
    let showForm = false;
    
	let board: Board;
	let timer: Timer;
    
    
    /////////////////////////////////
    function sendResult() {

        //const form = document.querySelector('form');
        const formURL = 'https://h9l4dxhaad.execute-api.eu-west-2.amazonaws.com/Prod/submitForm';
        const submitResponse = document.querySelector('#response');
        // Capture the form data
        let data = {
            username: $username,
            word_number: game.wordNumber, 
            num_guesses: (game.gameStatus === "WIN" ? game.guesses : "X"),
            time_in_secs: (game.timeEnd - game.timeStart) / 1000
        };
        //Array.from(form).map(input => (data[input.id] = input.value));
        console.log('Sending: ', JSON.stringify(data));
        submitResponse.innerHTML = 'Entering your result into the competition...'

        // Create the AJAX request
        var xhr = new XMLHttpRequest();
        xhr.open("POST", formURL, true);
        xhr.setRequestHeader('Accept', 'application/json; charset=utf-8');
        xhr.setRequestHeader('Content-Type', 'application/json; charset=UTF-8');

        // Send the collected data as JSON
        /////////////////////////////////////
        // Reinstate this line to activate email!
        xhr.send(JSON.stringify(data));
        /////////////////////////////////////
        xhr.onloadend = response => {
            if (response.target.status === 200) {
                //form.reset();
                submitResponse.innerHTML = 'Competition entry sent!';
                showForm = false;
            } else {
                submitResponse.innerHTML = 'Error sending your result!';
                showForm = true;
                console.error(JSON.parse(response));
            }
        };    
    };
    
    
    
    function updateKey(e,i) {
        var temp = $letterStates[game.boardState[game.guesses][i]] 
        switch(temp) {
            // Always update key if currently "nil" or "absent" 
            case "nil":
            case "absent":
                $letterStates[game.boardState[game.guesses][i]] = e;
                break;
            // Only update key when currently "present" if upgraded to "correct" 
            case "present":
                $letterStates[game.boardState[game.guesses][i]] = (e === "correct" ? e : temp);
                break;
            // Letter was correct: leave alone
            default:
                $letterStates[game.boardState[game.guesses][i]] = temp;
        }
    }
    
	function submitWord() {
		if (game.boardState[game.guesses].length !== COLS) {
			toaster.pop("Not enough letters");
			board.shake(game.guesses);
		} else if (words.contains(game.boardState[game.guesses])) {
			if (game.guesses > 0) {
				const hm = checkHardMode(game.boardState, game.evaluations, game.guesses);
				if ($hardMode) {
					if (hm.type === "correct") {
						toaster.pop(
							`${contractNum(hm.pos + 1)} letter must be ${hm.char.toUpperCase()}`
						);
						board.shake(game.guesses);
						return;
					} else if (hm.type === "present") {
						toaster.pop(`Guess must contain ${hm.char.toUpperCase()}`);
						board.shake(game.guesses);
						return;
					}
				} else if (hm.type !== "absent") {
					game.validHard = false;
				}
			}
			const state = getState(word, game.boardState[game.guesses]);
			game.evaluations[game.guesses] = state;
			state.forEach((e, i) => (updateKey(e,i)));
			++game.guesses;
			if (game.boardState[game.guesses - 1] === word) win();
			else if (game.guesses === ROWS) lose();
		} else {
			toaster.pop("Not in word list");
			board.shake(game.guesses);
		}
	}

	function win() {
		board.bounce(game.guesses - 1);
        game.gameStatus = "WIN";
        game.timeEnd = Date.now();
		setTimeout(() => toaster.pop(PRAISE[game.guesses - 1]), DELAY_INCREMENT * ROWS);
		setTimeout(() => (showStats = true), delay * 1.4);
		if (!modeData.modes[$mode].historical) {
			++stats.guesses[game.guesses];
			++stats.gamesPlayed;
			if ("currentStreak" in stats) {
				stats.currentStreak =
					modeData.modes[$mode].seed - stats.lastGame > modeData.modes[$mode].unit
						? 1
						: stats.currentStreak + 1;
				if (stats.currentStreak > stats.maxStreak) stats.maxStreak = stats.currentStreak;
			}
			stats.lastGame = modeData.modes[$mode].seed;
			localStorage.setItem(`statistics`, JSON.stringify(stats));
		}
        sendResult();
	}

	function lose() {
//		++game.guesses;
        game.gameStatus = "FAIL";
        game.timeEnd = Date.now();
        setTimeout(() => toaster.pop(word.toUpperCase()), DELAY_INCREMENT * ROWS);
		setTimeout(() => (showStats = true), delay);
		if (!modeData.modes[$mode].historical) {
			++stats.guesses.fail;
			++stats.gamesPlayed;
			if ("currentStreak" in stats) stats.currentStreak = 0;
			stats.lastGame = modeData.modes[$mode].seed;
			localStorage.setItem(`statistics`, JSON.stringify(stats));
		}
        sendResult();        
	}

	function reload() {
        if(getWordNumber() > 1)
            location.reload();            
		modeData.modes[$mode].seed = newSeed();
		game = createNewGame($mode);
        word = words.words[((getWordNumber() % words.words.length) + words.words.length) % words.words.length]
        $letterStates = createLetterStates();
		showStats = false;
		showRefresh = false;
		timer.reset($mode);
	}

	onMount(() => {
        if (stats.gamesPlayed === 0 || !($username)) {
            setTimeout(() => (showTutorial = true), delay);
        } else if (!(game.gameStatus === "IN_PROGRESS")) 
            setTimeout(() => (showStats = true), delay);
	});
	// $: toaster.pop(word);
</script>

<svelte:body on:click={board.hideCtx} on:contextmenu={board.hideCtx} />

	<Header
		bind:showRefresh
		showStats={stats.gamesPlayed > 0 || (modeData.modes[$mode].historical && !(game.gameStatus === "IN_PROGRESS"))}
		on:stats={() => (showStats = true)}
		on:tutorial={() => (showTutorial = true)}
		on:settings={() => (showSettings = true)}
		on:reload={reload}
	/>
<main class:guesses={game.guesses !== 0} style="--rows: {ROWS}; --cols: {COLS}">
    <div>
	<Board
		bind:this={board}
		bind:value={game.boardState}
		evaluations={game.evaluations}
		guesses={game.guesses}
	/>
    </div>
	<Keyboard
		on:keystroke={() => {
			board.hideCtx();
		}}
		bind:value={game.boardState[game.guesses === ROWS ? 0 : game.guesses]}
		on:submitWord={submitWord}
		on:esc={() => {
			showTutorial = false;
			showStats = false;
			showSettings = false;
		}}
		disabled={!(game.gameStatus === "IN_PROGRESS" && ($username))}
	/>
</main>

<Modal
	bind:visible={showTutorial}
>
	<Tutorial visible={showTutorial} />
</Modal>



<Modal bind:visible={showStats}>
<!--		<Statistics data={stats} /> -->
<!-- !(game.gameStatus === "IN_PROGRESS")} -->
    <h4 id="response">&nbsp;</h4>
    <Form 
        visible={showForm} 
        on:tryagain={sendResult}
    />

    <Distribution distribution={stats.guesses} guesses={game.guesses} active={game.gameStatus==="IN_PROGRESS"} />
	<Separator visible={!(game.gameStatus === "IN_PROGRESS")}>
		<Timer
			slot="1"
			bind:this={timer}
			on:timeup={() => (showRefresh = true)}
			on:reload={reload}
		/>
		<Share slot="2" state={game} />
	</Separator>
</Modal>

<Modal bind:visible={showSettings}>
	<Settings visible={showSettings} wordNumber={game.wordNumber} />
</Modal>

<style>
	main {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		height: calc(100% - var(--header-height));
		max-width: var(--game-width);
		margin: 0px auto;
		position: relative;
	}
</style>
